import React from 'react';
import { assets, workData } from '@/assets/assets';
import Image from 'next/image';
import { motion } from "framer-motion";

const Work = ({ isDarkMode }) => {
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 1 }}
      id="mywork"
      className="w-full px-10 lg:px-[12%] scroll-mt-20"
    >
      {/* Section Title */}
      <motion.h4 
        initial={{ y: -20, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.3, duration: 0.5 }}
        className="text-center mb-2 text-lg font-Ovo"
      >
        My Portfolio
      </motion.h4>

      <motion.h2
        initial={{ y: -20, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.3, duration: 0.5 }} 
        className="text-center text-5xl font-Ovo"
      >
        My Latest Work
      </motion.h2>

      <motion.p
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ delay: 0.7, duration: 0.5 }} 
        className="text-center max-w-2xl mx-auto mt-5 mb-12 font-Ovo"
      >
        I specialize in delivering high-quality development services tailored to your needs.
      </motion.p>

      {/* Work Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-5 my-10 dark:text-black">
        {workData.map((project, index) => (
          <motion.div
            key={index}
            whileHover={{ scale: 1.03 }}
            className="relative aspect-square rounded-lg overflow-hidden cursor-pointer group"
          >
            {/* Next.js Optimized Image */}
            <Image
              src={project.bgImage}
              alt={project.title}
              layout="fill"
              objectFit="cover"
              className="rounded-lg"
            />

            {/* Overlay Card */}
            <div className="bg-white w-10/12 rounded-md absolute bottom-5 left-1/2 transform -translate-x-1/2 py-3 px-5 flex items-center justify-between transition-all duration-500 group-hover:bottom-7 shadow-md">
              <div>
                <h2 className="text-lg font-semibold">{project.title}</h2>
                <p className="text-sm text-gray-600">{project.description}</p>
              </div>
              <div className="border rounded-full border-black w-9 aspect-square flex items-center justify-center shadow-[2px_2px_0_#000] group-hover:bg-lime-300 transition">
                <Image src={assets.send_icon} alt="send icon" className="w-5" />
              </div>
            </div>
          </motion.div>
        ))}
      </div>

      {/* Show More Button */}
      <motion.a
        initial={{opacity: 0}}
        whileInView={{opacity: 1}}
        transition={{delay: 1.1, duration: 0.5}}
        href="#"
        className="w-max flex items-center justify-center gap-2 text-gray-700 border-[0.5px] border-gray-700 rounded-full py-3 px-10 mx-auto my-20 hover:bg-lightHover duration-500 dark:text-white dark:border-white dark:hover:bg-darkHover"
      >
        Show More 
        <Image 
          src={isDarkMode ? assets.right_arrow_bold_dark : assets.right_arrow_bold} 
          alt="Right arrow" 
          className="w-4" 
        />
      </motion.a>
    </motion.div>
  );
};

export default Work;
