import React from 'react';
import Image from 'next/image';
import { motion } from 'framer-motion';
import { assets, serviceData } from '@/assets/assets';

const Services = ({ isDarkMode }) => {
  return (
    <motion.div 
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 1 }}
      id="services" 
      className="w-full px-10 lg:px-[12%] scroll-mt-20"
    >
      {/* Heading Section */}
      <motion.h4 
        initial={{ y: -20, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.3, duration: 0.5 }}
        className="text-center mb-2 text-lg font-Ovo"
      >
        Browse My Certificates
      </motion.h4>
      
      <motion.h2
        initial={{ y: -20, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ delay: 0.5, duration: 0.5 }} 
        className="text-center text-5xl font-Ovo"
      >
        My Certificates
      </motion.h2>

      <motion.p
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ delay: 0.7, duration: 0.5 }} 
        className="text-center max-w-2xl mx-auto mt-5 mb-12 font-Ovo"
      >
        Here are some of my certificates showcasing my technical skills.
      </motion.p>

      {/* Services Grid */}
      <motion.div 
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ delay: 0.9, duration: 0.6 }}
        className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 my-10"
      >
        {serviceData.map(({ title, description, bgImage, link }, index) => (
          <motion.div
            whileHover={{ scale: 1.05, boxShadow: "0px 10px 30px rgba(0, 0, 0, 0.1)" }} 
            key={index} 
            className="border border-gray-400 rounded-lg px-8 py-12 transition-all duration-500 cursor-pointer dark:hover:bg-darkHover dark:hover:shadow-white"
          >
            {/* Display certificate image */}
            {bgImage && (
              <Image 
                src={bgImage} 
                alt="Certificate" 
                width={500} 
                height={300} 
                unoptimized
                className="rounded-lg shadow-md"
              />
            )}

            <h3 className="text-lg my-4 text-gray-700 dark:text-white">{title}</h3>
            <p className="text-sm text-gray-600 leading-5 dark:text-white/80">{description}</p>

            {/* View Certificate Button */}
            {link && (
              <div className="mt-5 flex gap-4">
                {/* Open Certificate */}
                <a href={link} target="_blank" rel="noopener noreferrer"
                   className="text-sm text-blue-600 hover:underline">
                  View Certificate 
                </a>
              </div>
            )}
          </motion.div>
        ))}
      </motion.div>
    </motion.div>
  );
};

export default Services;
