import React, { useState, useEffect } from 'react';
import Image from 'next/image';
import { assets } from '@/assets/assets';

const Navbar = ({ isDarkMode, setIsDarkMode }) => {
  const [isScroll, setIsScroll] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  useEffect(() => {
    // Load dark mode preference from localStorage (default to light mode)
    const savedTheme = localStorage.getItem("theme") || "light";
    setIsDarkMode(savedTheme === "dark");

    // Apply the theme class to <html>
    if (savedTheme === "dark") {
      document.documentElement.classList.add("dark");
    } else {
      document.documentElement.classList.remove("dark");
    }

    // Scroll Effect
    const handleScroll = () => {
      setIsScroll(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const toggleDarkMode = () => {
    setIsDarkMode((prev) => {
      const newMode = !prev;
      localStorage.setItem("theme", newMode ? "dark" : "light");

      // Apply/remove dark mode class on <html>
      if (newMode) {
        document.documentElement.classList.add("dark");
      } else {
        document.documentElement.classList.remove("dark");
      }
      return newMode;
    });
  };

  return (
    <>
      {/* Background Image */}
      <div className="absolute top-0 w-11/12 -z-10 translate-y-[-80%] dark:hidden">
        <Image src={assets.header_bg_color} alt="Background" className="w-full" />
      </div>

      {/* Navbar */}
      <nav className={`w-full fixed px-5 lg:px-8 xl:px-[8%] py-4 flex items-center justify-between z-50 transition-all duration-300 ${
        isScroll ? 'bg-white bg-opacity-50 backdrop-blur-lg shadow-sm dark:bg-darkTheme dark:shadow-white/20' : ''}`}
      >
        {/* Logo */}
        <a href="#top" className="ml-6">
          <Image
            src={isDarkMode ? assets.logo_dark : assets.logo}
            alt="Logo"
            width={100}
            height={25}
            className="cursor-pointer"
          />
        </a>

        {/* Centered Menu */}
        <div className={`fixed left-1/2 transform -translate-x-1/2 px-6 lg:px-8 py-1 flex items-center z-50 transition-all duration-300 rounded-full backdrop-blur-md ${
          isScroll ? 'bg-gray-300/60 shadow-lg' : 'bg-gray-200/40 shadow-md'}`}
        >
          <ul className={`hidden md:flex items-center gap-6 lg:gap-8 rounded-full px-12 py-3 ${
            isScroll ? '' : 'bg-white shadow-sm bg-opacity-50 dark:border dark:border-white/50'}`}
          >
            {[
              { name: 'Home', id: 'home' },
              { name: 'About', id: 'about' },
              { name: 'Interships', id: 'certificate' },
              { name: 'My Work', id: 'mywork' },
              { name: 'Contact Me', id: 'contactme' },
            ].map((item, index) => (
              <li key={index}>
                <a
                  className="font-Ovo text-gray-700 hover:text-black transition dark:text-white dark:hover:text-gray-300"
                  href={`#${item.id}`}
                >
                  {item.name}
                </a>
              </li>
            ))}
          </ul>
        </div>

        <div className="flex items-center gap-4">
          {/* Dark Mode Toggle */}
          <button onClick={toggleDarkMode}>
            <Image
              src={isDarkMode ? assets.sun_icon : assets.moon_icon}
              alt="Dark Mode"
              width={24}
              height={24}
              className="w-6"
            />
          </button>

          {/* Contact Button */}
          <a
            href="#contactme"
            className="hidden lg:flex items-center gap-3 px-10 py-2.5 border border-gray-500 rounded-full ml-4 font-Ovo dark:border-white/50"
          >
            Contact
            <Image
              src={isDarkMode ? assets.arrow_icon_dark : assets.arrow_icon}
              alt="Arrow"
              width={12}
              height={12}
              className="w-3"
            />
          </a>

          {/* Mobile Menu Button */}
          <button className="block md:hidden ml-3" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            <Image
              src={isDarkMode ? assets.menu_white : assets.menu_black}
              alt="Menu"
              width={24}
              height={24}
              className="w-6"
            />
          </button>
        </div>
      </nav>

      {/* Mobile Navigation Menu */}
      <ul className={`fixed top-0 right-0 w-64 h-screen bg-rose-50 dark:bg-darkHover dark:text-white transition-transform duration-500 z-50 p-10 flex flex-col gap-4 ${
        isMenuOpen ? 'translate-x-0' : 'translate-x-[100%]'}`}
      >
        <div className="absolute top-6 right-6 cursor-pointer" onClick={() => setIsMenuOpen(false)}>
          <Image src={isDarkMode ? assets.close_white : assets.close_black} alt="Close Menu" className="w-5" />
        </div>
        {[
          { name: 'Home', id: 'home' },
          { name: 'About', id: 'about' },
          { name: 'Internships', id: 'certificate' },
          { name: 'My Work', id: 'mywork' },
          { name: 'Contact Me', id: 'contactme' },
        ].map((item, index) => (
          <li key={index}>
            <a
              className="font-Ovo"
              onClick={() => setIsMenuOpen(false)}
              href={`#${item.id}`}
            >
              {item.name}
            </a>
          </li>
        ))}
      </ul>
    </>
  );
};

export default Navbar;
